.image-gallery {
  flex: 0 1 100%;
  min-width: 320px;
}

.image-gallery-slide .image-gallery-image {
  height: 500px;
  object-fit: cover;
}

.image-gallery-thumbnail .image-gallery-thumbnail-image {
  height: 70px;
  object-fit: cover;
}

@media (max-width: 768px) {
  .image-gallery-slide .image-gallery-image {
    height: 350px;
    object-fit: cover;
  }  
}

@media (max-width: 450px) {
  .image-gallery-slide .image-gallery-image {
    height: 250px;
    object-fit: cover;
  }
  .image-gallery-slide .image-gallery-description {    
    bottom: 150px;
  }
}
